html, body {
  overflow-x: hidden;
}
body {
  position: relative
}

.App{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow-y: hidden;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../src/Assets/92b19a4aa6910ccbe1e0a5dbc4a212e6.jpeg");
  background-color: rgba(0, 0, 0, 0.54);
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100vw;
}

.nav{
  background-color: white;
  width: 500px;
  height: 80px;
  margin-top: 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  flex-shrink: 0;
  border-radius: 60px;
   box-shadow: 0px 4px 4px 0px rgba(0,0,0,0.25);
  height: 100px;
}

.logo{
  width: 165px;
  height: 41px;
}

.menu{
  display: none;
}

.menu2{
  display: none;
}

.pageTwo{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 0px;
}

.pageTwo h1{
  font-size: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pageTwo p{
  width: 884px;
  font-size: 25px;
  line-height: 34.1px;
  font-weight: 400px;
  text-align: center;
}

.join{
  margin-top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.join button{
  width: 224px;
  margin-top: 10px;
  color: #1637FF;
  height: 59px;
  border-radius: 8px;
  padding: 16px, 22px, 16px, 22px;
  display: flex;
  justify-content: center;
  align-items: center; 
}

.connect-container{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  gap: 50px;
  margin-top: 50px;
}

.share-text{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.share-text p{
  width: 638px;
  text-indent: 20px;
  word-wrap: break-word;
  height: 132px;
  text-align: center;
}

.share-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row-reverse;
  gap: 51px;
}

.promote-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.informed-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row-reverse;
}

.informed-text{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.informed-text p{
  width: 638px;
  height: 132px;
}

.footer{
  background-color: #1637FF;
  width: 200%;
  overflow-x: hidden;
  height: 94px
}

.top-footer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.top-footer p{
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
}

.logo2{
  height: 13px;
  width: 13px;
}

